import React from 'react';
import { ErrorBoundary } from '../../components/ErrorBoundary';
import { Footer } from '../../footer/Footer';
import { FooterUndo } from '../../footer/FooterUndo';
import { FooterRestart } from '../../footer/FooterRestart';
import { GimmickStore, useGimmickSub } from './gimmick-store';
import { StartupInfoPicker } from './StartupInfoPicker';
import { GimmickStarted } from './GimmickStarted';

type GimmickFooterProps = { started: boolean };

const GimmickFooter: React.FC<GimmickFooterProps> = ({ started }) => (
    <Footer>
        <FooterUndo onClick={GimmickStore.undo} />
        {started && <FooterRestart onClick={GimmickStore.restart} />}
    </Footer>
);

export const Gimmick: React.FC = () => {
    const started = useGimmickSub(({ players }) => !!players.length);

    return (
        <ErrorBoundary fix={GimmickStore.restart}>
            <>
                <div className="gimmick">{started ? <GimmickStarted /> : <StartupInfoPicker />}</div>
                <GimmickFooter started={started} />
            </>
        </ErrorBoundary>
    );
};
