import React, { useState } from 'react';
import { Morfi, FormData } from 'morfi';
import { FormInput } from '../../components/form/input';
import { __ } from 'first-intl';
import { Util } from '../../util/util';
import { PlayerState, SitStore } from './sit-store';
import { PlayerSelection } from '../../players/PlayerSelection';
import { Player, PlayerStore } from '../../players/player-store';

const newPlayer = (player: Player, playerIdx: number, rounds: number): PlayerState => ({
    player,
    points: Util.array(rounds).map(() => ({})),
    total: 0,
});

const playersFromStartup = (startupInfo: StartupInfo): PlayerState[] => {
    const rounds = Math.floor(startupInfo.numberOfCards / startupInfo.numberOfPlayers);
    return PlayerStore.get()
        .players.slice(0, startupInfo.numberOfPlayers)
        .map((player, index) => newPlayer(player, index, rounds));
};

const onChangeStartupInfo = (startupInfo: StartupInfo) => SitStore.set(playersFromStartup(startupInfo));

export type StartupInfo = { numberOfCards: number; numberOfPlayers: number };
export type StartupInfoValues = { numberOfCards: string; numberOfPlayers: string };
const StartupInfoValidations = {
    numberOfCards: {
        onChange: (v?: string) => {
            const num = Number(v);
            return isNaN(num) || num < 32 ? { id: 'SIT.startup.cartCount.invalid' } : undefined;
        },
    },
    numberOfPlayers: {
        onChange: (v?: string) => {
            const num = Number(v);
            if (isNaN(num) || num < 2 || num > 10) return { id: 'players.count.invalid', values: { min: 2, max: 10 } };
            if (num > PlayerStore.get().players.length) return { id: 'players.count.exceedsRegistered' };
        },
    },
};

const submit = ({ numberOfCards, numberOfPlayers }: StartupInfoValues) => {
    onChangeStartupInfo({ numberOfCards: +numberOfCards, numberOfPlayers: +numberOfPlayers });
};

const initialValues: StartupInfoValues = { numberOfCards: '52', numberOfPlayers: '' };
const { Form, Fields } = Morfi.create<StartupInfoValues>(initialValues);

export const StartupInfoPicker = () => {
    const [data, setData] = useState<FormData<StartupInfoValues>>({ values: initialValues, errors: {} });

    return (
        <div className="spade-is-trump">
            <div className="configs">
                <Form
                    className="startup"
                    onSubmit={submit}
                    validation={StartupInfoValidations}
                    data={data}
                    onChange={setData}>
                    <FormInput Field={Fields.numberOfCards} label="SIT.startup.cardCount" type="number" />
                    <FormInput Field={Fields.numberOfPlayers} label="players.count" type="number" />
                    <button type="submit">{__('APP.start')}</button>
                </Form>
            </div>
            <PlayerSelection />
        </div>
    );
};
