import React, { useCallback, useState } from 'react';
import { GIMMICK_ROW_SETTINGS, GimmickRow, GimmickStore } from './gimmick-store';
import { FormInput } from '../../components/form/input';
import { FormData, Morfi } from 'morfi';

export type ValueSelectionValues = { num: string };
const ValueSelectionValidations = {
    num: {
        onChange: (v?: string) => {
            const num = Number(v);
            if (isNaN(num) || num < 5 || num > 30) return { id: 'GIMMICK.value.invalid', values: { min: 5, max: 30 } };
        },
    },
};

const initialValues: ValueSelectionValues = { num: '' };
const { Form, Fields } = Morfi.create<ValueSelectionValues>(initialValues);

export type ValueSelectionOptionProps = { value: number; gimmickRow: GimmickRow; close: () => void };

export const ValueSelectionOption: React.FC<ValueSelectionOptionProps> = ({ value, gimmickRow, close }) => {
    const [data, setData] = useState<FormData<ValueSelectionValues>>({ values: initialValues, errors: {} });

    const onClick = useCallback(() => {
        GimmickStore.selectValue(value, gimmickRow);
        close();
    }, [value, gimmickRow, close]);

    const onSubmit = useCallback(
        ({ num }: ValueSelectionValues) => {
            GimmickStore.selectValue(+num, gimmickRow);
            close();
        },
        [gimmickRow, close]
    );

    return (
        <div className="value" onClick={value === -1 ? undefined : onClick}>
            {value === -1 ? (
                <Form onSubmit={onSubmit} validation={ValueSelectionValidations} data={data} onChange={setData}>
                    <FormInput Field={Fields.num} type="number" />
                </Form>
            ) : value === 0 ? (
                '✘'
            ) : (
                value
            )}
        </div>
    );
};

type ValueSelectionProps = { gimmickRow: GimmickRow; close: () => void };

export const ValueSelection: React.FC<ValueSelectionProps> = ({ gimmickRow, close }) => {
    const { multiplier, fixedValue } = GIMMICK_ROW_SETTINGS[gimmickRow];

    return (
        <div className="select-overlay">
            <div className="select-overlay-inner">
                {multiplier
                    ? [0, 1, 2, 3, 4, 5, 6].map((v) => (
                          <ValueSelectionOption key={v} gimmickRow={gimmickRow} value={v} close={close} />
                      ))
                    : fixedValue
                    ? [0, fixedValue].map((v) => (
                          <ValueSelectionOption key={v} gimmickRow={gimmickRow} value={v} close={close} />
                      ))
                    : [0, -1].map((v) => (
                          <ValueSelectionOption key={v} gimmickRow={gimmickRow} value={v} close={close} />
                      ))}
            </div>
            <div className="select-overlay-outer" onClick={close} />
        </div>
    );
};
