import { Persistore } from 'persistore';
import { PointsState } from './Points';
import { Player } from '../../players/player-store';
import { createStore } from 'react-use-sub';

const _key = 'SIT.history';

export type PlayerState = { player: Player; points: PointsState[]; total: number };
export type GameState = PlayerState[];

const getHistory = (): GameState[] => {
    try {
        const players = Persistore.get(_key);
        return JSON.parse(players as any);
    } catch (e) {
        return [];
    }
};

const get = (): GameState => getHistory()[0] || [];

type SpadeIsTrumpState = { gameState: GameState };

const [useSpadeIsTrumpSub, store] = createStore<SpadeIsTrumpState>({ gameState: get() });

export { useSpadeIsTrumpSub };

const _set = (gameState: GameState): void => {
    const history = getHistory();
    history.unshift(gameState);
    if (history.length > 10) history.pop();
    Persistore.set(_key, JSON.stringify(history));
};

const set = (update: GameState | ((gameState: GameState) => GameState)) => {
    if (typeof update === 'function') {
        store.set(({ gameState }) => ({ gameState: update(gameState) }));
    } else {
        store.set({ gameState: update });
    }
    _set(store.get().gameState);
};

const reset = () => SitStore.set([]);

const undo = (): void => {
    const history = getHistory();
    if (history.length > 1) history.shift();
    Persistore.set(_key, JSON.stringify(history));
    store.set({ gameState: history[0] });
};

const _clear = (state: GameState): GameState =>
    state.map((player) => ({
        ...player,
        total: 0,
        points: player.points.map(() => ({})),
    }));

const rearrange = () =>
    set((state) => {
        const cleared = _clear(state);
        return [...cleared.slice(1), cleared[0]];
    });

export const SitStore = { set, get, undo, reset, rearrange };
