import { Persistore } from 'persistore';
import { createStore } from 'react-use-sub';
import { Util } from '../util/util';

const _key = 'players';

export type Player = { uuid: string; name: string; shortcut?: string };

const _get = (): Player[] => {
    try {
        const players = Persistore.get(_key);
        return JSON.parse(players as any);
    } catch (e) {
        return [];
    }
};

const [usePlayerSub, store] = createStore<{ players: Player[] }>({ players: _get() });

export { usePlayerSub };

const _set = (next: Player[]): void => Persistore.set(_key, JSON.stringify(next));

const set: typeof store['set'] = (updates) => {
    store.set(updates);
    _set(store.get().players);
};

const add = (name: string, shortcut?: string) =>
    set(({ players }) => ({ players: [{ name, shortcut, uuid: Util.uuid() }, ...players] }));

// TODO: Integrate the possibility to change players
// const update = (update: Player): void =>
//     set(({ players }) => {
//         const index = players.findIndex((p) => p.uuid === update.uuid);
//         return { players: index === -1 ? players : Util.nextArray(players, index, update) };
//     });

const remove = (player: Player): void =>
    set(({ players }) => ({ players: players.filter((p) => p.uuid !== player.uuid) }));

const _moveTo = (before: number, after: number) =>
    set(({ players }) => {
        const next = players.filter((_, index) => index !== before);
        next.splice(after, 0, players[before]);
        return { players: next };
    });

const moveUp = (current: number) => (current > 0 ? _moveTo(current, current - 1) : undefined);
const moveDown = (current: number) => _moveTo(current, current + 1);

export const PlayerStore = { add, remove, moveDown, moveUp, get: store.get };
