import React, { ReactNode, useCallback, useState } from 'react';
import { ClickableDescription } from '../SpadeIsTrump/ClickableDescription';
import { GimmickRow, GimmickStore, useGimmickSub } from './gimmick-store';
import { ValueSelection } from './ValueSelection';
import { Util } from '../../util/util';
import { Cube } from '../Again/Cube';
import { __string } from 'first-intl';

export const GIMMICK_NODES: { [key in GimmickRow]: ReactNode } = {
    [GimmickRow.M1]: <Cube count={1} />,
    [GimmickRow.M2]: <Cube count={2} />,
    [GimmickRow.M3]: <Cube count={3} />,
    [GimmickRow.M4]: <Cube count={4} />,
    [GimmickRow.M5]: <Cube count={5} />,
    [GimmickRow.M6]: <Cube count={6} />,
    [GimmickRow.TRIPLE]: __string('GIMMICK.triple'),
    [GimmickRow.FOURSOME]: __string('GIMMICK.foursome'),
    [GimmickRow.FULL_HOUSE]: __string('GIMMICK.fullHouse'),
    [GimmickRow.SMALL_STREET]: __string('GIMMICK.smallStreet'),
    [GimmickRow.BIG_STREET]: __string('GIMMICK.smallStreet'),
    [GimmickRow.SENSATION]: __string('GIMMICK.bigStreet'),
    [GimmickRow.CHANCE]: __string('GIMMICK.chance'),
};

export const GimmickStarted: React.FC = () => {
    const [selection, setSelection] = useState<GimmickRow | undefined>();
    const closeSelection = useCallback(() => setSelection(undefined), []);
    const players = useGimmickSub(({ players }) => players);
    const activePlayer = GimmickStore.getCurrentActivePlayer(players);
    return (
        <>
            <table>
                <thead>
                    <tr>
                        <th className="first" />
                        {players.map(({ name, shortcut, uuid }, playerIdx) => (
                            <ClickableDescription
                                desc={name}
                                short={shortcut}
                                key={playerIdx}
                                tag="th"
                                className={Util.classNames(activePlayer.uuid === uuid && 'active')}
                            />
                        ))}
                    </tr>
                </thead>
                <tbody>
                    {Object.values(GimmickRow).map((row) => (
                        <tr key={row}>
                            <td className="first">{GIMMICK_NODES[row]}</td>
                            {players.map(({ points, uuid }) => (
                                <td
                                    className={Util.classNames(activePlayer.uuid === uuid || 'inactive')}
                                    key={uuid}
                                    // eslint-disable-next-line
                                    onClick={
                                        points[row] === undefined && activePlayer.uuid === uuid
                                            ? () => setSelection(row)
                                            : undefined
                                    }>
                                    {points[row] === 0 ? '✘' : points[row]}
                                </td>
                            ))}
                        </tr>
                    ))}
                    <tr>
                        <ClickableDescription desc={__string('GIMMICK.sum')} short="∑" tag="td" />
                        {players.map((player) => (
                            <td key={player.uuid}>{GimmickStore.calculateSum(player)}</td>
                        ))}
                    </tr>
                </tbody>
            </table>
            {selection && <ValueSelection gimmickRow={selection} close={closeSelection} />}
        </>
    );
};
