import { Persistore } from 'persistore';
import { createStore } from 'react-use-sub';
import { Player, PlayerStore } from '../../players/player-store';
import { Util } from '../../util/util';

export enum GimmickRow {
    M1 = 'M1',
    M2 = 'M2',
    M3 = 'M3',
    M4 = 'M4',
    M5 = 'M5',
    M6 = 'M6',
    TRIPLE = 'TRIPLE',
    FOURSOME = 'FOURSOME',
    FULL_HOUSE = 'FULL_HOUSE',
    SMALL_STREET = 'SMALL_STREET',
    BIG_STREET = 'BIG_STREET',
    SENSATION = 'SENSATION',
    CHANCE = 'CHANCE',
}

/**
 * If has "multiplier" it will display X + 1 ... 6 and multiplies with the multiplier when calculating the points.
 * Else if has "fixedValue" it will display X + <fixedValue> as selectable points.
 * Else it will display X + Free-Number-Text-Option (values from 5 to 30 are allowed).
 */
export type GimmickRowSettings = { multiplier?: number; fixedValue?: number };

export const GIMMICK_ROW_SETTINGS: { [key in GimmickRow]: GimmickRowSettings } = {
    [GimmickRow.M1]: { multiplier: 1 },
    [GimmickRow.M2]: { multiplier: 2 },
    [GimmickRow.M3]: { multiplier: 3 },
    [GimmickRow.M4]: { multiplier: 4 },
    [GimmickRow.M5]: { multiplier: 5 },
    [GimmickRow.M6]: { multiplier: 6 },
    [GimmickRow.TRIPLE]: {},
    [GimmickRow.FOURSOME]: {},
    [GimmickRow.FULL_HOUSE]: { fixedValue: 25 },
    [GimmickRow.SMALL_STREET]: { fixedValue: 30 },
    [GimmickRow.BIG_STREET]: { fixedValue: 40 },
    [GimmickRow.SENSATION]: { fixedValue: 50 },
    [GimmickRow.CHANCE]: {},
};

export type PlayerState = Player & { points: Partial<{ [key in GimmickRow]: number }> };

type GimmickState = {
    players: PlayerState[];
};

const initialGimmickState: GimmickState = {
    players: [],
};

const _key = 'gimmick';

const getHistory = (): GimmickState[] => {
    try {
        const states = Persistore.get(_key);
        return JSON.parse(states as any);
    } catch (e) {
        return [];
    }
};

const get = (): GimmickState => getHistory()[0] || initialGimmickState;

const [useGimmickSub, store] = createStore<GimmickState>(get());

export { useGimmickSub };

const set: typeof store['set'] = (updates) => {
    store.set(updates);
    const updatedState = store.get();

    const history = getHistory();
    history.unshift(updatedState);
    if (history.length > 20) history.pop();
    Persistore.set(_key, JSON.stringify(history));
};

const restart = (): void => {
    set(initialGimmickState);
};

const undo = (): void => {
    const history = getHistory();
    if (history.length > 1) {
        history.shift();
        Persistore.set(_key, JSON.stringify(history));
        const prevState = history[0];
        store.set(prevState);
    }
};

const _newPlayer = (player: Player): PlayerState => ({
    ...player,
    points: {},
});

const selectPlayers = (numberOfPlayers: number) => {
    const players = PlayerStore.get().players.slice(0, numberOfPlayers).map(_newPlayer);
    set({ players });
};

const getCurrentActivePlayer = (players: PlayerState[]): PlayerState => {
    const minSelected = Math.min(...players.map(({ points }) => Object.keys(points).length));
    return players.find(({ points }) => Object.keys(points).length === minSelected)!;
};

const _updatePlayer = (player: PlayerState) =>
    set(({ players }) => {
        const copy = [...players];
        const index = copy.findIndex(({ uuid }) => uuid === player.uuid);
        copy[index] = player;

        return { players: copy };
    });

const selectValue = (value: number, gimmickRow: GimmickRow) => {
    const { multiplier } = GIMMICK_ROW_SETTINGS[gimmickRow];
    const chosen = multiplier ? multiplier * value : value;
    const player = getCurrentActivePlayer(store.get().players);
    player.points[gimmickRow] = chosen;
    _updatePlayer(player);
};

const calculateSum = ({ points }: PlayerState) => {
    const allPoints = Util.sum(...Object.values(points));
    const multiplierPoints = Util.sum(
        points[GimmickRow.M1],
        points[GimmickRow.M2],
        points[GimmickRow.M3],
        points[GimmickRow.M4],
        points[GimmickRow.M5],
        points[GimmickRow.M6]
    );
    const bonus = multiplierPoints >= 63 ? 35 : 0;
    return allPoints + bonus;
};

export const GimmickStore = { restart, undo, selectPlayers, selectValue, getCurrentActivePlayer, calculateSum };
