import React from 'react';
import { PlayerStore, usePlayerSub } from './player-store';
import { PlayerRow } from './PlayerRow';
import { AddPlayer } from './AddPlayer';

export const PlayerSelection: React.FC = () => {
    const players = usePlayerSub(({ players }) => players);

    return (
        <div className="players">
            <div className="registered-players">
                <table>
                    <tbody>
                        {players.map((player, index) => (
                            <PlayerRow player={player} pos={index} key={player.uuid} />
                        ))}
                    </tbody>
                </table>
            </div>
            <AddPlayer onAdd={PlayerStore.add} />
        </div>
    );
};
