import React, { useState } from 'react';
import { Morfi, FormData } from 'morfi';
import { FormInput } from '../../components/form/input';
import { __ } from 'first-intl';
import { GimmickStore } from './gimmick-store';
import { PlayerSelection } from '../../players/PlayerSelection';
import { PlayerStore } from '../../players/player-store';

export type StartupInfoValues = { numberOfPlayers: string };
const StartupInfoValidations = {
    numberOfPlayers: {
        onChange: (v?: string) => {
            const num = Number(v);
            if (isNaN(num) || num < 1 || num > 10) return { id: 'players.count.invalid', values: { min: 1, max: 10 } };
            if (num > PlayerStore.get().players.length) return { id: 'players.count.exceedsRegistered' };
        },
    },
};

const submit = ({ numberOfPlayers }: StartupInfoValues) => {
    GimmickStore.selectPlayers(+numberOfPlayers);
};

const initialValues: StartupInfoValues = { numberOfPlayers: '' };
const { Form, Fields } = Morfi.create<StartupInfoValues>(initialValues);

export const StartupInfoPicker = () => {
    const [data, setData] = useState<FormData<StartupInfoValues>>({ values: initialValues, errors: {} });

    return (
        <div className="spade-is-trump">
            <div className="configs">
                <Form
                    className="startup"
                    onSubmit={submit}
                    validation={StartupInfoValidations}
                    data={data}
                    onChange={setData}>
                    <FormInput Field={Fields.numberOfPlayers} label="players.count" type="number" />
                    <button type="submit">{__('APP.start')}</button>
                </Form>
            </div>
            <PlayerSelection />
        </div>
    );
};
