import React, { useCallback, useState } from 'react';
import { Morfi, FormData } from 'morfi';
import { FormInput } from '../components/form/input';
import { __ } from 'first-intl';

type AddPlayerFormValues = { name: string; shortcut: string };
const initialValues: AddPlayerFormValues = { name: '', shortcut: '' };
const { Form, Fields } = Morfi.create<AddPlayerFormValues>(initialValues);
const initialData: FormData<AddPlayerFormValues> = { values: initialValues, errors: {} };
const validation = { name: { onChange: (v?: string) => (!v ? { id: 'players.add.name.required' } : undefined) } };

type AddPlayerFormProps = { close: () => void; onAdd: (name: string, shortcut: string) => void };

export const AddPlayerForm: React.FC<AddPlayerFormProps> = ({ close, onAdd }) => {
    const [data, setData] = useState(initialData);

    const onSubmit = useCallback(
        ({ name, shortcut }) => {
            onAdd(name, shortcut);
            close();
        },
        [close, onAdd]
    );

    return (
        <Form
            className="startup add-players"
            onSubmit={onSubmit}
            validation={validation}
            data={data}
            onChange={setData}>
            <FormInput autoFocus Field={Fields.name} label="players.add.name" />
            <FormInput Field={Fields.shortcut} label="players.add.shortcut" />
            <button type="submit">{__('players.add.submit')}</button>
        </Form>
    );
};

type AddPlayerProps = { onAdd: (name: string, shortcut: string) => void };

export const AddPlayer: React.FC<AddPlayerProps> = ({ onAdd }) => {
    const [focus, setFocus] = useState(false);

    const onClick = useCallback(() => setFocus(true), []);
    const close = useCallback(() => setFocus(false), []);

    return focus ? (
        <AddPlayerForm close={close} onAdd={onAdd} />
    ) : (
        <button className="add-player" onClick={onClick} type="submit">
            {__('players.add')}
        </button>
    );
};
